#top-bar-elements-container{
    width: 70vw;
    max-width: 1300px;
}
#top-bar-title{
    font-size: 28px;
}
#top-line{
    width: 90vw;
    max-width: 1500px;
}
#links-container{
    width: 200px;
}


@media screen and (max-width: 768px){
    #top-bar-elements-container{
        width: 85vw;
    }
    #top-bar-title{
        font-size: 24px;
    }
    #top-line{
        width: 90vw;
    }
    #links-container{
        width: 150px;
    }
}